.menu-toggle {
  display: none;

  @include vp-767 {
    @media (max-width: ($vp-768 - 1px)) {
      display: block;
      position: absolute;
      top: 6px;
      right: 2px;
      width: 24px;
      height: 14px;
      transition-duration: 1s;
      cursor: pointer;
      border: none;
      padding: 24px;
      background: transparent;
      z-index: 20;
    }
  }

  @include focus {
    &.focus-visible:focus {
      outline: 1px solid #00c8ff;
    }
  }

  @mixin hover {
    @media (hover: hover) {
      &:hover:not(.focus-visible) {
        background: $primary-color;
      }
    }
  }
}

.nav--open .menu-toggle {
  padding: 20px;
  top: 10px;
  right: -15px;
}

.menu-toggle__line {
  height: 2px;
  width: 24px;
  background: $second-color;
  position: absolute;
  left: 11px;
  transition-duration: 0.25s;
  transition-delay: 0.25s;

  &::before {
    content: "";
    left: 0;
    position: absolute;
    top: -6px;
    height: 2px;
    width: 24px;
    background: $second-color;
    transition: transform 0.25s, top 0.25s 0.25s;
  }

  &::after {
    content: "";
    left: 0;
    position: absolute;
    top: 6px;
    height: 2px;
    width: 24px;
    background: $second-color;
    transition: transform 0.25s, top 0.25s 0.25s;
  }
}

.nav--open .menu-toggle__line {
  transition-duration: 0.1s;
  transition-delay: 0.25s;
  background: transparent;
}

.nav--open .menu-toggle .menu-toggle__line::before {
  transition: top 0.25s, transform 0.25s 0.25s;
  top: 0;
  transform: rotateZ(-45deg);
  background: $primary-color;
  width: 19px;
}

.nav--open .menu-toggle .menu-toggle__line::after {
  transition: top 0.4s, transform 0.25s 0.25s;
  top: 0;
  transform: rotateZ(45deg);
  background: $primary-color;
  width: 19px;
}
